.container {
  text-align: left;
  border: 1px solid rgba(12,18,28,.12);
  border-top: 0;
  max-height: 300px;
  overflow: scroll;
}

.pre {
  padding: 0;
  margin: 0;
  overflow-x: auto;
  white-space: pre-wrap;
  word-wrap: break-word;
}

.code {
  composes: font-body-static-xs from global;
  font-family: Menlo;
  /* color: var(--color-text-primary); */
  font-size: 90%;
  margin-top: 0;
  margin-bottom: 0;
}

.copy {
  position: absolute;
  bottom: 21px;
  right: 36px;
  z-index: 1;
  padding: 5px 13px;
  border-radius: 2px;
  cursor: pointer;
  font-weight: bold;
  background: #007bff;
  color: white;
}
